
import Grid from '@material-ui/core/Grid';
import { Theme } from '@material-ui/core/styles/createMuiTheme';
import createStyles from '@material-ui/core/styles/createStyles';
import withStyles, { WithStyles } from '@material-ui/core/styles/withStyles';
import Typography from '@material-ui/core/Typography';
import * as React from 'react';
import withRoot from './withRoot';
// const url = (name: string, wrap = false) => `${wrap ? 'url(' : ''}https://awv3node-homepage.surge.sh/build/assets/${name}.svg${wrap ? ')' : ''}`


const styles = (theme: Theme) =>
    createStyles({
        root: {
            paddingTop: theme.spacing.unit * 20,
            textAlign: 'center',

        },
    });

interface IState {
    open: boolean;
}

class App extends React.Component<WithStyles<typeof styles>, IState> {
    public state = {
        open: false,
    };
    private parallax: any

    public handleClose = () => {
        this.setState({
            open: false,
        });
    };

    public handleClick = () => {
        this.setState({
            open: true,
        });
    };

    public refParallax = (ref: any) => (this.parallax = ref);

    public scrollTo = (event: any) => (page: number) => {
        this.parallax.scrollTo(page)
    }


    public render() {
        return (

            <Grid
                style={{ marginTop: '60px' }}
                container={true}
                spacing={40}
                direction="row"
                justify="center"
                alignItems="center">

                <Grid item={true} sm={12}  >
                    <Typography align='center' style={{ color: 'white' }} variant="h3" gutterBottom={true} >
                        I'm steeve and I love changing things, thinking about the future and creating.
                    </Typography>

                    <Typography align='center' style={{ color: 'white' }} variant="h3" gutterBottom={true} >
                        Watch this!
                    </Typography>


                </Grid>

                <Grid item={true} lg={3} style={{ border: '2px solid white' }}>
                    <Typography align='center' style={{ color: 'white' }} variant="h3" gutterBottom={true} >
                        My Work
                    </Typography>
                </Grid>

                <Grid item={true} lg={3} style={{ border: '2px solid white' }}>
                    <Typography align='center' style={{ color: 'white' }} variant="h3" gutterBottom={true} >
                        About 
                    </Typography>
                </Grid>
                <Grid item={true} lg={3} style={{ border: '2px solid white' }} >
                    <Typography align='center' style={{ color: 'white' }} variant="h3" gutterBottom={true} >
                        My Blog
                    </Typography>
                </Grid>


            </Grid>



        );
    }
}

export default withRoot(withStyles(styles)(App));


